@import url(https://fonts.googleapis.com/css?family=Karla:400,700&display=swap);
*{padding:0px;margin:0px;border:0px}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}aside,nav,footer,header,section{display:block}html,body{height:100%;margin:0;padding:0;position:relative;width:100%;color:#000}body{font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-weight:inherit;font-size:inherit}body{margin:0;font-family:'Karla', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body.lock{overflow:hidden}@media (max-width: 770px){body.lock{width:100%;position:fixed;overflow:hidden}}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.footer{padding:20px;display:flex;justify-content:space-between;align-items:center;background-color:#494949;color:#ffffff;font-size:16px;line-height:24px;flex-wrap:wrap}.footer a{color:#ffffff;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s}.footer a:hover{color:#b2bbc4}.footer__logo{max-width:120px}.footer__logo img{width:67px}.footer__terms{max-width:120px}.footer__help{margin:0 2% 0 0}.footer__subscribe{min-width:270px}.help{max-width:165px}.help__title{color:#b2bbc4;text-transform:uppercase;font-size:14px}.subscribe__title{color:#b2bbc4;font-size:20px;margin-bottom:10px}.subscribe__form{border:1px solid #b2bbc4;display:flex;justify-content:center;align-items:center;font-size:0;line-height:0}.subscribe__input{flex:1 1 auto;margin:15px 0 15px 15px}.subscribe__input input{width:100%;font-size:16px;background-color:#494949;font-family:Karla;color:#b2bbc4}.subscribe__submit{margin:auto 15px}.social{display:flex;font-size:0;margin:15px 0}.social__link{height:34px;width:34px;fill:#ffffff;margin:0 7.5px;-webkit-transition:all 0.3s ease 0.3s;transition:all 0.3s ease 0.3s}.social__link:hover{fill:#b2bbc4}

.top-bar{background-color:#F7F7F8;display:flex;justify-content:space-between;align-items:center;padding:10px 10px 10px 35px}.top-bar__right{display:flex;flex-wrap:nowrap}.top-bar__logo img{height:30px;max-width:300px}.top-bar a{color:#494949;font-size:16px;font-style:normal;margin:0 35px 0 0}.top-bar a:hover{text-decoration:underline}

.header__navbar{padding:10px 35px 10px 35px;background-color:#fdfdfd;box-shadow:0 6px 4px -2px rgba(0,0,0,0.07)}.navbar{display:flex;justify-content:space-between;flex-wrap:nowrap}.navbar__menu-list{flex:1 1 auto;justify-content:space-between;display:flex;align-items:center}.navbar__menu-list li{display:inline-block}.navbar__menu-list a{color:#b2bbc4;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s;text-transform:uppercase;-moz-tab-size:16px;tab-size:16px}.navbar__menu-list a:hover{color:#494949}.navbar__menu-list img{height:25px}.navbar__menu-search{margin-left:55px}.navbar__menu-search img{height:25px}.navbar__jordan a{background-repeat:no-repeat;display:inline-block;width:26px;height:25px;background-image:url(../../static/media/jumpman.7cf22dbb.svg);background-size:35px 24px;cursor:pointer;background-position:center;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s}.navbar__jordan a:hover{background-image:url(../../static/media/jumpman_hover.07f78f6b.svg)}.navbar__nike a{background-repeat:no-repeat;display:inline-block;width:68px;height:25px;background-image:url(../../static/media/nike_sport.51c8d918.svg);background-size:35px 24px;cursor:pointer;background-position:center;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s}.navbar__nike a:hover{background-image:url(../../static/media/nike_sport_hover.c7ef2e9d.svg)}.navbar__adidas a{background-repeat:no-repeat;display:inline-block;width:40px;height:25px;background-image:url(../../static/media/adidas.71c8c727.svg);background-size:35px 24px;cursor:pointer;background-position:center;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s}.navbar__adidas a:hover{background-image:url(../../static/media/adidas_hover.df6b52bf.svg)}

.header{position:fixed;top:0;left:0;width:100%;z-index:3}

.awssld__bullets{bottom:0 !important;z-index:2}.awssld__bullets--active{-webkit-transform:scale(1.2) !important;transform:scale(1.2) !important}.awssld__content{background-image:url(../../static/media/r-background.0ce12a17.svg);border:none;background-size:56%;background-repeat:no-repeat;background-position:-5% 30%;margin-bottom:0;width:99.8%;background-color:#ffffff}.awssld__wrapper{max-height:650px}.slide-button{width:159px;height:50px;border:1px solid #b2bbc4;color:#b2bbc4;display:inline-block;text-align:center}.slide-button:hover{background-color:#494949;color:#ffffff}.slide{margin-top:95px;position:relative;font-size:46px;text-transform:uppercase;line-height:56px;color:#494949}.slide img{width:100%}.slide a{font-size:16px;line-height:48px;color:#494949}.slide__1-text{justify-content:center;align-items:center;position:absolute;top:0.5em;left:0;right:0;margin:auto;text-align:center;max-width:300px}.slide__2-text{position:absolute;top:20%;left:100px;max-width:360px}.slide__3-text{position:absolute;top:20%;left:100px;max-width:350px}

